/*article page*/
.post-user{
    padding: 10px 15px;
    display: flex;
    border-bottom: 1px solid #ddd;
}
.post-user-avar{
    width: 60px;
}
.post-user-avar img{
    width: 80%;
}
.post-nickname{
    font-weight: 600;
}
.post-user-tags{
    display: flex;
    flex-wrap: wrap;
}
.article-container{
    padding: 10px 15px;
    border-bottom: 1px solid #ddd;
}
.article-page-title{
    font-size: 1.6rem;
    color: #3a3a3a;
    font-weight: bold;
}
.article-page-tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}
.article-page-tag{
    margin: 5px 20px 5px 0px;
    font-size: 1.2rem;
    color: #8e9eaf;
}
.article-content{
    font-size: 1.4rem;
    color: #333333;
    line-height: 22px;
}
.article-content p{
    margin: 10px 0px 20px 0px;
}
.social-bar{
    display: flex;
}
.share-area{
    width: 50%;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
}
.share-area i{
    margin: 0px 5px;
}
.comments-area{
    display: flex;
    font-size: 1.2rem;
    margin-left: auto;
    color: #8e9eaf;
}
.thumbs-up{
    display: flex;
    align-items: center;
    margin-right: 10px;
}
.comments-count{
    display: flex;
    align-items: center;
}
.comments-area i{
    margin: 0px 2px;
}
.comments-container{
    margin-bottom: 50px;
}
.comments-bar{
    display: flex;
    align-items: center;
    padding: 10px 15px;
    border-bottom: 1px solid #ddd;
}
.comments-bar-tip{
    font-size: 1.4rem;
    color: #4d6983;
}
.comments-form{
    padding: 10px 15px;
    border-bottom: 1px solid #ddd;
}
.comments-type{
    margin-left: auto;
    font-size: 1.2rem;
}
.comments-item{
    display: flex;
    padding: 10px 15px;
}
.comments-avar{
    width: 60px;
}
.comments-avar img{
    width: 80%;
}
.comments-detail{
    width: calc( 100% - 60px );
    border-bottom: 1px solid #ddd;
}
.comments-title-area{
    display: flex;
}
.comments-title{
    font-size: 1.4rem;
    color: #4d6983;
}
.response_to{
    text-decoration: underline;
}
.comments-reply{
    margin-left: auto;
    font-size: 1.2rem;
}
.comments-content{
    margin: 10px 0px;
    font-size: 1.4rem;
    color: #333;
}
.comments-input-bar{
    width: 100%;
    height: 40px;
    position: fixed;
    bottom: 0px;
    border-top: 1px solid #ddd;
    background: #fff;
}
.comments-input-bar input{
    width: calc( 100% - 40px );
    height: 40px;
    border: 0px;
    outline: none;
    line-height: 40px;
    padding: 0px 15px;
}
.comments-input-bar .icon-send{
    position: absolute;
    top: 8px;
    right: 10px;
}
.comments-btns{
    display: flex;
    margin-top: 5px;
}
.button-comment{
    padding: 7px 15px;
    font-size: 1.4rem;
    line-height: 1.4rem;
    margin-left: auto;
}


