/**
 * mobile reset
 */
* {
    box-sizing: border-box;
}
body,a,input{
    color: #445768;
}
html {
    /*background: #efefef;*/
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-tap-highlight-color: transparent;
    overflow-x: hidden;
    font-size: .625rem
}
body,html {
    min-height: 100vh;
}
body, input, textarea, select{
    font-family: "microsoft yahei",tahoma,Helvetica,'鍗庢枃缁嗛粦',arial,'\5b8b\4f53','Hiragino Sans GB',sans-serif
}
body {
    font-size: 1.5rem;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent
}
h1,h2,h3,h4,h5,h6,ul,li,em,strong,pre,code, button {
    line-height: 1.5rem;
    font-size: 100%;
    font-style: normal
}
article,aside,blockquote,body,button,code,dd,details,div,dl,dt,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,hr,input,legend,li,menu,nav,ol,p,pre,section,td,textarea,th,ul {
    margin: 0;
    padding: 0
}
input,select,textarea {
    font-size: 100%;
    box-shadow: none;
    -webkit-appearance: none;
}
input:focus{
    box-shadow: none;
    outline: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0
}
fieldset,img, button {
    border: 0
}
abbr,acronym {
    border: 0;
    font-variant: normal
}
del {
    text-decoration: line-through
}
address,caption,cite,code,dfn,em,th,var {
    font-style: normal;
}
ol,ul {
    list-style: none
}
caption,th {
    text-align: left
}
q:after,q:before {
    content: ''
}
sub,sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline
}
sup {
    top: -.5em
}
sub {
    bottom: -.25em
}
a,ins {
    text-decoration: none
}
ul {
    list-style: none
}
img {
    border: 0;
    display: block;
}
p {
    margin: 0
}
a,a:link,a:visited,a:hover,a:active {
    text-decoration: none
}
textarea{
    resize: none;
}

/*base style*/
body img{
    max-width: 100%;
}
.input-sign{
    padding: 5px 10px;
    border: 0px;
    outline: none;
    border-bottom: 1px solid #cfdcd9;  
}
.input-info{
    padding: 10px 15px;
    border: 1px solid #e4e4e4;
    outline: none;
    border-radius: 4px;
    width: 100%;
}
.textarea-default{
    padding: 10px;
    border: 1px solid #e4e4e4;
    outline: none;
    border-radius: 4px;
    width: 100%;
}
.ft-bar-next{
    width: 100%;
    position: fixed;
    bottom: 0px;
    padding: 15px 15px;
    background: #49ced4;
    text-align: center;
    color: #fff;
    border: 0px;
}
.ft-bar-skip-next{
    width: 100%;
    position: fixed;
    bottom: 0px;
    padding: 15px 15px;
    background: #49ced4;
    border: 0px;
}
.ft-bar-skip-next .ft-bar-tab{
    width: 50%;
    float: left;
    color: #fff;
    text-align: center;
}
.ft-bar-skip-next .ft-bar-tab:first-child{
    border-right: 1px solid #fff;
}
.piece{
    font-size: 1.5rem;
    font-weight: bold;
    color: #4d6983;
    padding: 10px 0px;
}
.piece-with-date{
    display: flex;
    align-items: center;
}
.piece-with-date .piece-date-picker{
    color: #999;
    font-size: 1.2rem;
    margin-left: auto;
}
.piece-date-picker .icon-calendar{
    position: relative;
    top: 2px;
}
.picker-yes{
    color: #4d6983;
}
.info-card{
    margin-bottom: 15px;
}
.custom-radio{
    padding: 7px 15px;
    font-size: 1.4rem;
    background: #fff;
    border: 1px solid #49ced4;
    color: #49ced4;
    border-radius: 5px;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
}
.custom-radio-filled{
    background: #49ced4;
    color: #fff;
}
.custom-checkbox{
    padding: 7px 15px;
    font-size: 1.4rem;
    background: #fff;
    border: 1px solid #49ced4;
    color: #49ced4;
    border-radius: 5px;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
}
.custom-checkbox-filled{
    background: #49ced4;
    color: #fff;
}
.button-default{
    padding: 10px 20px;
    background: #49ced4;
    border: 1px solid #49ced4;
    outline: none;
    color: #fff;
    border-radius: 5px;
    font-size: 1.7rem;
    line-height: 1.7rem;
}
.button-gray{
    padding: 10px 20px;
    background: #f5f5f5;
    outline: none;
    color: #4d6983;
    border-radius: 5px;
    font-size: 1.7rem;
    line-height: 1.7rem;
}
.button-default:active{
    background: #48bec3;
}
.button-gray:active{
    background: #e1e1e1;
}
.hidden{
